const body = document.querySelector('body');

//    Loader
export function spinnerPlay() {
  //   'loading'   body
  body.classList.add('loading');
}


//    Loader
export function spinnerStop() {
  window.setTimeout(function () {
    //   'loading'   body
    body.classList.remove('loading');
    //  Loader
    body.classList.add('loaded');
  }, 1500);
}